.individual-leaderboard {
    width: 85%;
    display: flex;
    flex-direction: column;
}

.leaderboard-title-and-navigation {
    display: grid;
    grid-template-columns: 70% 30%;
    margin-bottom: 3vh;
}

.leaderboard-spotlight-row {
    display: grid;
    grid-template-columns: 70% 25%;
    padding: 1%;
    column-gap: 5%;
}

@media (max-width: 1500px) {
    .leaderboard-spotlight-row {
        grid-template-columns: 50% 50%;
        column-gap: 0%;
    }
}