.news-feed-title {
    font-size: 28px;
    padding: 0.5%;
    background-color: #404d72;
    color: white;
    border-radius: 5px;
}

.source-field,
.summary-field {
    margin-bottom: 1vh;
}

.create-post-fieldset {
    color: #242d75;
    padding: 1%;
    padding-top: 0%;
}

.create-post-submit, 
.create-post-close {
    color: #242d75;
    width: 15%;
    padding: 1%;
    cursor: pointer;
    outline: none;
    border: none;
}

.create-post-submit:hover,
.create-post-close:hover {
    background-color: #242d75cc;
    color: white;
    transition: 0.2s ease;
}

.home-page-rss-feed {
    list-style: none;
}

.news-feed-post {
    background-color: rgba(247, 247, 247, 0.233);
    margin-top: 1%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 2%;
}

.post-author {
    display: flex;
    flex-direction: row;
}

.post-author > img {
    width: 7%;
    border-radius: 50%;
}

.post-author > h3 {
    padding-top: 2%;
    padding-left: 1%;
}

.post-author-details {
    display: flex;
    flex-direction: column;
    padding-left: 1%;
}

.post-votes {
    display: flex;
    flex-direction: row;
    padding-top: 2%;
    margin-left: 65%;
    /* border: 1px solid black; */
}

.post-author-description {
    margin-top: 1%;
}

.post-news-preview {
    display: flex;
    flex-direction: row;
    padding: 2%;
    background-color: rgba(216, 211, 211, 0.13);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0%;
    margin-top: 1%;
    width: 100%;
}

.post-news-preview > a {
    text-decoration: none;
    color: #16295e;
}

.post-news-preview > a > img {
    border-radius: 10%;
}

.post-news-preview > a > h3 {
    padding-top: 2%;
    padding-left: 2%;
}

.post-news-preview:hover {
    background-color: rgba(146, 145, 145, 0.13);
}

.post-news-title {
    width: 100%;
}

@media (max-width: 1100px) {
    .home-page-news-feed-container {
        font-size: 0.9em;
    }
    .source-field {
        width: 100%;
    }
    .summary-field {
        width: 100%;
    }
    .create-post-submit {
        width: 20%;
    }
    .author-profile-pic {
        height: 6vh;
    }
}