:root {
    --main-theme: #404d75;
    --main-theme-stats: #aab6d818;
    --main-theme-hover: rgba(255, 255, 255, 0.876);
}

.navbar {
    background-color: var(--main-theme);
    height: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

nav {
    display: flex;
    flex-direction: column;
}

.nav-logo {
    margin-top: -130px;
    width: 100%;
}

.nav-profile-pic {
    margin-top: -10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
    max-width: 256px;
    overflow: hidden;
    object-fit: fill;
}

.nav-profile-pic > img {
    height: 100%;
    width: 75%;
    border: 3px solid white;
    border-radius: 105px;
}

.nav-menu {
    background-color: var(--main-theme);
    width: 250px;
    height: 100vh;
    width: 12vw;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    /* left: -100%; */
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text,
.nav-text-stats {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    color: #f5f5f5;
    font-size: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0 16px;
}

.nav-text-stats {
    background-color: var(--main-theme-stats);
    font-size: 18px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: var(--main-theme-hover);
    color: black;
    transition: 0.1s ease-in-out;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: var(--main-theme);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.nav-title {
    margin-left: 16px;
}

@media (max-width: 1500px) {
    .nav-menu {
        width: 20%;
    }
    .nav-title {
        font-size: 0.9em;
    }
}