.profile-stats {
    display: flex;
    flex-direction: column;
}

.profile-stats > h1 {
    padding: 1% 1% 0% 1%;
}

.profile-stats > h3 {
    padding-left: 1%;
}

.profile-stats-inner {
    padding: 1.5%;
    padding-top: 1%;
    display: flex;
    flex-direction: row;
}

.profile-stats-recent-forecasts {
    display: flex;
    flex-direction: column;
    width: 75%;
    margin-right: 1vw;
}

.profile-stats-selectors {
    list-style: none;
    display: flex;
    flex-direction: row;
    margin-bottom: 1vh;
}

.profile-stats-selectors > li{
    padding: 1px;
    /* border: 1px outset lightgray; */
    /* box-shadow: 1px 1px 5px #888888; */
    cursor: pointer;
}

.profile-stats-selectors > li:hover {
    background-color: #404d72;
    color: white;
    transition: 0.2s ease;
}

.profile-stats-grid {
    display: grid;
    grid-template-rows: 10% 10% 10%;
    width: 30%;
}

.profile-stats-grid-headers {
    display: grid;
    grid-template-columns: 50% 50%;
    text-align: center;
}

.profile-stats-grid-body {
    display: grid;
    grid-template-columns: 45% 0% 60%;
    text-align: center;
}

@media (max-width: 1500px) {
    .profile-stats-inner {
        display: flex;
        flex-direction: column;
    }
    .profile-stats-recent-forecasts,
    .profile-stats-grid {
        width: 100%;
    }
    .profile-stats-grid-headers {
        margin-top: 1vh;
    }
    .profile-stats-grid-body {
        margin-top: 3vh;
    }
}