.topics-menu {
    text-align: center;
    border: 1px outset darkgray;
    border-radius: 8px;
    box-shadow: 2px 2px 10px #888888;
    padding: 2%;
}

.topics-list {
    list-style: none;
    padding-top: 1vh;
}

.topics-list > li {
    padding: 3%;
    margin-bottom: 1vh;
    border: 1px solid lightgray;
    border-radius: 8px;
}

.topics-list > li:hover {
    background-color: #242d7565;
    color: white;
    cursor: pointer;
}

.topics-list > hr {
    width: 50%;
    margin: 0 auto;
}