.home {
    width: 85%;
    display: flex;
    flex-direction: column;
    /* background-color: lightgray; */
}

.home-page-stats-div {
    display: flex;
    flex-direction: column;
    row-gap: 1%;
}

.home-page-div {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 2vh;
    /* border: 1px solid blue; */
}

.home-page-grid {
    display: grid;
    grid-template-columns: 65% 33%;
    column-gap: 2%;
}

.create-post-button {
    width: 100%;
    height: 8vh;
    margin: 0 auto;
    margin-top: 1vh;
    background-color: rgb(243, 241, 241);
    border: 3px dashed rgba(128, 128, 128, 0.603);
    font-size: 1.5em;
    cursor: pointer;
    outline: none;
}

.create-post-button:hover {
    background-color: rgba(243, 241, 241, 0.555);
}

.home-page-news-feed {
    border: 1px solid darkgray;
    /* border-radius: 8px;
    box-shadow: 1px 1px 3px #888888; */
    padding: 5px;
    /* max-height: 100vh;
    overflow-y: auto; */
}

.button-holder-div {
    display: grid;
    width: 100%;
    grid-template-columns: 25% 25% 25%;
    grid-template-rows: 22vh;
    row-gap: 3%;
    column-gap: 5%;
    margin-bottom: 4vh;
    justify-content: center;
    /* border: 3px solid yellow; */
}

@media (max-width: 1100px) {
    .home-page-grid {
        grid-template-columns: 100%;
    }
    .onboarding-div {
        font-size: 0.97em;
    }
    .home-page-news-feed {
        width: 100%;
    }
}

