.profile {
    width: 85%;
    display: flex;
    flex-direction: column;
}

.main-profile-grid {
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
}

.profile-grid-left {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25% 40%;
    margin: 0 auto;
    row-gap: 11.5vh;
    width: 90%;
    height: 47vh;
    border: 1px outset lightgray;
    box-shadow: 1px 1px 5px #888888;
}

.profile-grid-left-profile-pic {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    overflow: hidden;
}

.profile-menu-option {
    padding: 5%;
}

.profile-grid-right {
    border: 1px outset lightgray;
    box-shadow: 1px 1px 5px #888888;
    margin-bottom: 4vh;
}