.profile-right-pane {
    padding: 1%;
}

.profile-right-pane-grid {
    display: grid;
    grid-template-columns: 20% 80%;
    grid-template-rows: 20% 80%;
}

.profile-profile-pic {
    width: 85%;
    max-width: 85%;
    margin: 0 auto;
    margin-top: 5%;
    border-radius: 8%;
    border: 1px solid lightgray;
}
