.onboarding {
    display: flex;
    flex-direction: column;
    height: 25vh;
    padding: 10px;
    border: 1px outset darkgray;
    border-radius: 8px;
    /* box-shadow: 1px 1px 5px #888888; */
    margin-bottom: 3vh;
}

.onboarding-incomplete {
    display: flex;
    flex-direction: column;
    height: 6.5vh;
    padding: 10px;
    border: 1px outset darkgray;
    border-radius: 8px;
    /* box-shadow: 1px 1px 5px #888888; */
    margin-bottom: 3vh;
}

.onboarding-title-div {
    display: grid;
    grid-template-columns: 95% 5%;
    padding: 0.3%;
    margin-bottom: 1vh;
    background-color: #404d72;
    color: white;
    border-radius: 5px;
}

.show-hide-onboarding-btn {
    cursor: pointer;
}

.show-hide-onboarding-btn:hover {
    background-color: #6e78c2;
    color: #fff;
    outline: none;
    transition: 0.2s ease;
}

.onboarding-list {
    list-style: none;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
}

.onboarding-list-item {
    border: 2px solid #242d75;
    border-radius: 10%;
    padding: 0.5%;
    text-align: center;
    margin-left: 1em;
    margin-right: 1em;
    width: 10vw;
}

.onboarding-list-item:hover {
    background-color: #7b80a8;
    cursor: pointer;
    color: white;
    transition: 0.2s ease;
}

.onboarding-list-item-picture {
    width: 55%;
    height: 70%;
    border: 1px solid black;
    border-radius: 50%;;
}

@media (max-width: 1100px) {
    .onboarding {
        height: 23vh;
    }
    .onboarding-list-item {
        height: 15vh;
        width: 12.5vw;
        margin-left: 0.5em;
        margin-right: 0.5em;
    }
    .onboarding-list-item-picture {
        height: 50%;
    }
}