.learn {
    width: 85%;
    display: flex;
    flex-direction: column;
}

.learn-grid {
    display: grid;
    grid-template-columns: 27% 72%;
    column-gap: 1%;
    margin-top: 5vh;
}

.topic-pane {
    border: 1px outset darkgray;
    border-radius: 8px;
    box-shadow: 2px 2px 10px #888888;
}