.leaderboard-grid {
    display: grid;
    width: 95%;
    grid-template-columns: 25% 25% 25% 25%;
    margin: 0% 1% 1% 1%;
    /* padding-bottom: 1vh; */
}

@media (max-width: 1500px) {
    .leaderboard-grid {
        grid-template-columns: 50% 50%;
        font-size: 0.85em;
    }
}

@media (max-width: 600px) {
    .leaderboard-grid {
        grid-template-columns: 100%;
    }
}