@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home,
.forecast,
.individual-leaderboard,
.leaderboard-menu,
.learn,
.profile {
  display: flex;
  height: 100vh;
  margin: 3vh 0vw 4vh 13.3vw;
  /* top right bottom left */
}

@media (max-width: 1500px) {
  .home,
  .forecast,
  .individual-leaderboard,
  .leaderboard-menu,
  .learn,
  .profile {
    padding-left: 8vw;
  }
}
:root {
    --main-theme: #404d75;
    --main-theme-stats: #aab6d818;
    --main-theme-hover: rgba(255, 255, 255, 0.876);
}

.navbar {
    background-color: #404d75;
    background-color: var(--main-theme);
    height: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

nav {
    display: flex;
    flex-direction: column;
}

.nav-logo {
    margin-top: -130px;
    width: 100%;
}

.nav-profile-pic {
    margin-top: -10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
    max-width: 256px;
    overflow: hidden;
    object-fit: fill;
}

.nav-profile-pic > img {
    height: 100%;
    width: 75%;
    border: 3px solid white;
    border-radius: 105px;
}

.nav-menu {
    background-color: #404d75;
    background-color: var(--main-theme);
    width: 250px;
    height: 100vh;
    width: 12vw;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    /* left: -100%; */
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text,
.nav-text-stats {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    color: #f5f5f5;
    font-size: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0 16px;
}

.nav-text-stats {
    background-color: #aab6d818;
    background-color: var(--main-theme-stats);
    font-size: 18px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: rgba(255, 255, 255, 0.876);
    background-color: var(--main-theme-hover);
    color: black;
    transition: 0.1s ease-in-out;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #404d75;
    background-color: var(--main-theme);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.nav-title {
    margin-left: 16px;
}

@media (max-width: 1500px) {
    .nav-menu {
        width: 20%;
    }
    .nav-title {
        font-size: 0.9em;
    }
}
.home {
    width: 85%;
    display: flex;
    flex-direction: column;
    /* background-color: lightgray; */
}

.home-page-stats-div {
    display: flex;
    flex-direction: column;
    grid-row-gap: 1%;
    row-gap: 1%;
}

.home-page-div {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 2vh;
    /* border: 1px solid blue; */
}

.home-page-grid {
    display: grid;
    grid-template-columns: 65% 33%;
    grid-column-gap: 2%;
    -webkit-column-gap: 2%;
            column-gap: 2%;
}

.create-post-button {
    width: 100%;
    height: 8vh;
    margin: 0 auto;
    margin-top: 1vh;
    background-color: rgb(243, 241, 241);
    border: 3px dashed rgba(128, 128, 128, 0.603);
    font-size: 1.5em;
    cursor: pointer;
    outline: none;
}

.create-post-button:hover {
    background-color: rgba(243, 241, 241, 0.555);
}

.home-page-news-feed {
    border: 1px solid darkgray;
    /* border-radius: 8px;
    box-shadow: 1px 1px 3px #888888; */
    padding: 5px;
    /* max-height: 100vh;
    overflow-y: auto; */
}

.button-holder-div {
    display: grid;
    width: 100%;
    grid-template-columns: 25% 25% 25%;
    grid-template-rows: 22vh;
    grid-row-gap: 3%;
    row-gap: 3%;
    grid-column-gap: 5%;
    -webkit-column-gap: 5%;
            column-gap: 5%;
    margin-bottom: 4vh;
    justify-content: center;
    /* border: 3px solid yellow; */
}

@media (max-width: 1100px) {
    .home-page-grid {
        grid-template-columns: 100%;
    }
    .onboarding-div {
        font-size: 0.97em;
    }
    .home-page-news-feed {
        width: 100%;
    }
}


.home-header-intro {
    height: 10%;
    margin-bottom: 2vh;
}

@media (max-width: 1500px) {
    .home-header-intro {
        font-size: 0.9em;
    }
}
.news-feed-title {
    font-size: 28px;
    padding: 0.5%;
    background-color: #404d72;
    color: white;
    border-radius: 5px;
}

.source-field,
