}

.post-author-details {
    display: flex;
    flex-direction: column;
    padding-left: 1%;
}

.post-votes {
    display: flex;
    flex-direction: row;
    padding-top: 2%;
    margin-left: 65%;
    /* border: 1px solid black; */
}

.post-author-description {
    margin-top: 1%;
}

.post-news-preview {
    display: flex;
