    background-color: rgba(146, 145, 145, 0.13);
}

.post-news-title {
    width: 100%;
}

@media (max-width: 1100px) {
    .home-page-news-feed-container {
        font-size: 0.9em;
    }
    .source-field {
        width: 100%;
    }
