    grid-template-rows: 10% 10% 10%;
    width: 30%;
}

.profile-stats-grid-headers {
    display: grid;
    grid-template-columns: 50% 50%;
    text-align: center;
}

.profile-stats-grid-body {
    display: grid;
    grid-template-columns: 45% 0% 60%;
    text-align: center;
}

@media (max-width: 1500px) {
    .profile-stats-inner {
        display: flex;
        flex-direction: column;
    }
    .profile-stats-recent-forecasts,
    .profile-stats-grid {
        width: 100%;
    }
    .profile-stats-grid-headers {
        margin-top: 1vh;
    }
    .profile-stats-grid-body {
        margin-top: 3vh;
    }
}
.profile-details {
    height: 40vh;
}
.settings {
    height: 40vh;
}
