.leaderboard {
    width: 100%;
    height: 80vh;
    padding-left: 1vw;
    padding-right: 1vw;
    /* border: 2px dashed green; */
    background-color: rgba(241, 241, 241, 0.103);
}

.leaderboard-table {
    /* border: 1px solid blue; */
    border-collapse: collapse;
    width: 100%;
}

.leaderboard-title-row > th {
    background-color: #636baa;
    color: white;
    font-size: 1.2em;
}

.leaderboard-table > tr {
    height: 40px;
}

.leaderboard-title-row > th,
.leaderboard-row > td {
    border-left: 1px solid lightgrey;
}

.leaderboard-title-row > th:first-child,
.leaderboard-row > td:first-child {
    border-left: none;
}

.position-column {
    width: 5%;
    border: 1px dotted black;
}

.leaderboard-rank-data,
.leaderboard-ffPoints-data,
.leaderboard-avgBrierScore-data {
    text-align: center;
}

.username-column {
    width: 45%;
    border: 1px dotted black;
}

.leaderboard-username-data {
    padding-left: 1vw;
}

.leaderboards-profile-pic {
    width: 3%;
    border-radius: 40%;
    vertical-align: middle;
    margin-right: 1%;
}

.ffpoints-column,
.avg-brier-column {
    width: 10%;
    border: 1px dotted black;
}

@media (max-width: 1500px) {
    .leaderboard-title-row {
        font-size: 0.8em;
    }
    .username-column {
        width: 25%;
    }
    .leaderboards-profile-pic {
        width: 1.8em;
    }
}