.home-button-nav-button {
    margin: 10px;
    padding: 5px;
    font-size: 20px;
    border-radius: 5px;
    border: 1px solid grey;
    background-color: rgb(61, 201, 248);
    cursor: pointer;
}

.home-button-nav-button:hover {
    background-color: rgb(68, 149, 177);
    color: white;
    transition: 0.3s ease;
}