.top-three-users-highlight {
    width: 60%;
    display: flex;
    flex-direction: row;
}

.top-three-individual-user {
    padding: 1%;
    width: 33%;
    text-align: center;
}

.top-three-individual-user > img {
    width: 70%;
    height: 75%;
    border-radius: 8px;
}

.rank-one-profile-pic {
    border: 3px solid gold;
}

.rank-two-profile-pic {
    border: 3px solid silver;
}

.rank-three-profile-pic {
    border: 3px solid #cd7f32;
}

@media (max-width: 1500px) {
    .top-three-users-highlight {
        width: 100%;
        font-size: 0.75em;
        margin-bottom: 1vh;
    }
    .top-three-individual-user > img {
        height: 65%;
        width: 75%;
    }
}