.topic {
    display: flex;
    flex-direction: column;
    border: 1px solid blue;
}

.topic-title {
    text-align: center;
    padding: 1%;
    text-decoration: underline;
}

.topic-youtube-video {
    width: 50%;
    margin: 0 auto;
}

.topic-to-quiz-button {
    background-color: #404d75;
    color: white;
    padding: 0.5% 2.5%;
    width: 5%;
    margin: 0vh 1vw 1vh 90%;
    cursor: pointer;
}

.topic-to-quiz-button:hover {
    background-color: lightblue;
    color: black;
    transition: 0.2s ease;
}

@media (max-width: 1500px) {
    .topic-youtube-video {
        width: 95%;
    }
    .topic-youtube-video > iframe {
        width: 100%;
    }
    .topic-to-quiz-button {
        width: 10%;
    }
}