.home-button-small {
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: 1px solid darkgray;
    margin-bottom: 1vh;
    /* border-radius: 8px;
    box-shadow: 1px 1px 5px #888888; */
}

.home-button-small:hover {
    box-shadow: 2px 2px 10px #888888;
}

.home-button-small-title {
    display: grid;
    grid-template-columns: 95% 5%;
    padding: 1.5%;
    background-color: #404d72;
    color: white;
    border-radius: 5px;
}

.home-button-nav-right {
    border: 1px solid white;
    padding: 1px;
    border-radius: 3px;
}

.home-button-small-data {
    font-size: 50px;
    text-align: center;
    padding: 10px;
}

.home-button-small-subtitle {
    text-align: center;
}

.home-button-scroll {
    cursor: pointer;
}