.leaderboard-navigation {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: flex-end;
    padding-right: 2vw;
}

.navigation-button {
    display: flex;
    flex-direction: row;
    background-color: white;
    outline: none;
    border: none;
    cursor: pointer;
}

.navigation-button:hover {
    background-color: #242d7517;
}

@media (max-width: 1500px) {
    .navigation-button {
        font-size: 0.8em;
    }
}